@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: Frak;
  src: url("/public/assets/fonts/PPFraktionMono-Regular.woff2");
  font-weight: normal;
}
@font-face {
  font-family: Frak;
  src: url("/public/assets/fonts/PPFraktionMono-Bold.woff2");
  font-weight: bold;
}
@font-face {
  font-family: Frak;
  src: url("/public/assets/fonts/PPFraktionMono-Light.woff2");
  font-weight: lighter;
}


body {
  font-family: Frak;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
