.item {
    background-color: #4159d000;
background-image: linear-gradient(43deg, #4159d015 0%, #c850c01e 46%, #ffcd701c 100%);

}
.item2 {
    background: #b2459100;  /* fallback for old browsers */
    background: linear-gradient(to right, #f15f7a2f, #b245913d); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    
}
.item3 {
    background: #5514b000;  /* fallback for old browsers */
background: linear-gradient(to right, #dbd75c2d, #5514b021); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.item4 {
    background: linear-gradient(to right, #64b3f41a, #c2e59c21); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    
}
.default {
    background-color: rgba(255, 255, 255, 0.05);
}


.item_lol {
    background-color: #4159d0;
background-image: linear-gradient(43deg, #4159d0 0%, #c850c0 46%, #ffcd70 100%);

}
.item2_lol {
    background: #b24591;  /* fallback for old browsers */
    background: linear-gradient(to right, #f15f7a, #b24591); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    
}
.item3_lol {
    background: #5514b0;  /* fallback for old browsers */
background: linear-gradient(to right, #dbd75c, #5514b0); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.item4_lol {
    background: linear-gradient(to right, #64b3f4, #c2e59c); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    
}
.default_lol {
    background-color: rgba(255, 255, 255, 0.05);
}